import React, { useState } from 'react';
import people from './data';
import { FaChevronLeft, FaChevronRight, FaQuoteRight } from 'react-icons/fa';

const Review = () => {
  const [index, setIndex] = useState(0);
  const {name, job, image, text, id} = people[index];

  const randomPerson = () => {
    setIndex(() => {
      let randomIndex = Math.floor(Math.random() * people.length);
      return check(randomIndex);
    })
  }

  const check = (numb) => {
    if(numb > people.length-1){
      return 0;
    }

    if(numb < 0) {
      return people.length-1;
    }

    return numb;
  }

  const next = () => {
    setIndex((index) => {
      let newIndex = index + 1;
      return check(newIndex);
    })
  }

  const prev = () => {
    setIndex((index) => {
      let newIndex = index - 1;
      return check(newIndex);
    })
  }
  return (
    <article className="review">
      <div className="img-container">
        <img className="person-img" src={image} alt={name} />
        <span className="quote-icon">
          <FaQuoteRight />
        </span>
      </div>
      <h4 className="author">{name}</h4>
      <p className="job">{job}</p>
      <p>{text}</p>
      <div className="button-container">
        <button className="prev-btn" onClick={prev}><FaChevronLeft /></button>
        <button className="next-btn" onClick={next}><FaChevronRight /></button>
      </div>
      <button className="random-btn" onClick={randomPerson}>Random</button> 
    </article>
  );
};

export default Review;
